body{
  font-family: sans-serif;
  text-align: center;
  background-color: #2a6871;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  
}

.container{
  height: 400px;
  width: 300px;
  background: #2b5870;
  border-radius: 20px;
  box-shadow: 10px 10px 38px  0px rgba(0, 0, 0, 0.75);
}

.temperature-display-container{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 70%;
  }

  .temperature-display{
    display: flex;
    border-radius: 50%;
    color: #ffffff;
    height: 220px;
    width: 220px;
    text-align: center;
    justify-content: center;
    align-items: center;
    font-size: 48px;
    border: 3px #ffffff solid;
    
  }

  button{
    border-radius: 100px;
    height: 60px;
    width: 60px;
    font-size: 32px;
    color: #ffffff;
    background: rgb(105, 104, 104);
    border: 2px #ffffff solid;
  }
  button:hover{
    background: rgb(184, 184, 184);
    cursor: pointer;
  }
  button:focus{
    outline: 0;
  }

  .button-container{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
  }

  .neutral{
    background: rgb(184, 184, 184);
  }
  .cold{
    background: #035aa6;
  }
  .hot{
    background: #ff5200;
  }

